import axios from "axios"
import Footer from "@/components/Footer.vue"
export default {
    name: "MyUser",
    data() {
        return {
            userId : localStorage.getItem("userId"),
            username: localStorage.getItem("username"),
            emailUser:  localStorage.getItem("email"),
            phone: localStorage.getItem("phone"),
            adress: localStorage.getItem("adress"),
            showContent: false,
            biography: localStorage.getItem("biography"),
            editBio: "",
            
            baseUrl: `https://backend-turtle-a84we87d5-tiagobonito44-hotmailcom.vercel.app/user/`
        }
    },
    
    created() {
        const userId = localStorage.getItem("userId");
         axios.get(`https://backend-turtle-a84we87d5-tiagobonito44-hotmailcom.vercel.app/takeUser/${userId}`)
        .then(response => {
            const user = response.data 
          console.log(response)
          this.biography = user.biography
        })
        .catch(error => {
          console.error(error);
        });
      },
      mounted() {
        document.body.style.background = "#f2ece7"; 
    },
    methods : {
         newBiography () {
            this.showContent = !this.showContent
    
        },

        editBiography() {
            const newBiographyData = {
                biography : this.editBio
             }
             axios.put(`${this.baseUrl}${this.userId}/biography`, newBiographyData)
             .then((res) => {
                this.showContent = !this.showContent
              
                 console.log(res)
                 this.biography = localStorage.setItem("biography", this.biography)
                this.biography = res.data.biography
               
                 
                 
              
                this.editBio = ""
             }) 
             .catch((err) => {
                 console.log(err)
             })
        }
    },  
    
    components: {
       Footer
    }
}